import { all, takeLatest, call, put } from "redux-saga/effects";
import { getTokenMap, getTokenMapStatus } from './reducer';


import { exchangeAPI } from '../apis/api';
import { getIcon } from '../../utils/swap_calc_utils';
import { CoinInfo, CoinMap } from '@loopring-web/component-lib/static-resource/index';

const getTokenMapApi = async ()=>{
    const {tokenSymbolMap: tokens} = (  await exchangeAPI().getTokens())
    const keys = Reflect.ownKeys(tokens);
    let coinMap: CoinMap<any, CoinInfo<any>> = {}
    keys.forEach((key: any) => {
        if (!(key as string).startsWith('LP-')) {
            coinMap[ key ] = {
                icon: getIcon(key, tokens),
                name: key,
                simpleName: key,
                description: '',
                company: '',
            }
        }
    })

    return {data:coinMap}
 };
export function* getPostsSaga() {
    try {
        const {data}  = yield call(getTokenMapApi);
        yield put(getTokenMapStatus({tokenMap:data}));
    } catch (err) {
        yield put(getTokenMapStatus(err));
    }
}

export default function* tokenSaga() {
    yield all([takeLatest(getTokenMap, getPostsSaga)]);
}
